.header {
  width: 1110px;
  background-color: #373f68;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 27px 32px;
  display: flex;
  justify-content: space-between;
}
.header__title {
  margin-top: 4px;
  font-weight: 700;
  font-size: 24px;
  line-height: 35px;
  letter-spacing: -0.333333px;
  color: #ffffff;
}
